<template>
    <div class="app-container">
        <!--头部区域-->
        <el-card class="operate-top-card" shadow="never">
            <div>
                <i class="el-icon-search" style="font-size: 18px; margin-right: 3px;"></i>
                <span>筛选搜索</span>
                <el-button
                        class="btn-find"
                        size="mini"
                        type="primary"
                        style="float: right; margin-left: 10px;"
                        @click="handleSearchList"
                >
                    查询订单
                </el-button>
                <el-button
                        class="btn-clear"
                        size="mini"
                        style="float: right;"
                        @click="handleResetSearch"
                >
                    清空
                </el-button>

            </div>
            <div style="margin-top: 50px;">
                <el-form
                        ref="productCateForm"
                        :model="listQuery"
                        label-width="150px"
                        :inline="true"
                >
                    <el-form-item label="订单编号：" >
                        <el-input v-model="listQuery.orderSn" placeholder="请输入订单编号"></el-input>
                    </el-form-item>
                    <el-form-item label="订单状态：">
                        <el-select v-model="listQuery.status" placeholder="请选择订单的状态">
                            <el-option
                                    v-for="item in statusOptions"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="订单分类:">
                        <el-select v-model="listQuery.orderType" placeholder="请选择订单分类">
                            <el-option
                                    v-for="item in orderTypeOptions"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="订单来源:">
                        <el-select v-model="listQuery.sourceType" placeholder="请选择订单来源">
                            <el-option
                                    v-for="item in sourceTypeOptions"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="下单时间:">
                        <el-date-picker
                            v-model="listQuery.orderTime"
                            type="date"
                            value-format="yyyy-MM-dd"
                            placeholder="选择日期"
                        >
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="收货人：">
                        <el-input v-model="listQuery.receiverName" placeholder="请输入收货人姓名"></el-input>
                    </el-form-item>
                </el-form>
            </div>
        </el-card>
        <!--列表-->
        <div>
            <!--头部区域-->
            <el-card class="operate-top-card" shadow="never">
                <i class="el-icon-s-order" style="font-size: 18px; margin-right: 3px;"></i>
                <span>订单列表</span>
            </el-card>
            <!--内容区域-->
            <div class="table-container">
                <el-table
                    ref="multipleTable"
                    :data="list"
                    v-loading="listLoading"
                    border
                    style="width: 100%"
                    @selection-change="handleSelectionChange"
                >
                    <el-table-column type="selection" width="55"></el-table-column>
                    <el-table-column label="序号" width="60" align="center">
                        <template slot-scope="scope">{{(scope.$index + 1) + ((listQuery.pageNum-1)* listQuery.pageSize)}}</template>
                    </el-table-column>
                    <el-table-column label="订单编号"  width="100" align="center">
                        <template slot-scope="scope">{{scope.row.orderSn}}</template>
                    </el-table-column>
                    <el-table-column label="用户账号" align="center">
                        <template slot-scope="scope">{{scope.row.memberUsername}}</template>
                    </el-table-column>
                    <el-table-column label="下单时间"  width="160" align="center">
                        <template slot-scope="scope">{{scope.row.orderTime}}</template>
                    </el-table-column>
                    <el-table-column label="订单金额" align="center">
                        <template slot-scope="scope">¥{{scope.row.totalAmount}}</template>
                    </el-table-column>
                    <el-table-column label="支付方式" align="center">
                        <template slot-scope="scope">{{scope.row.payType | formatPayType}}</template>
                    </el-table-column>
                    <el-table-column label="订单来源" align="center">
                        <template slot-scope="scope">{{scope.row.sourceType  | formatSourceType}}</template>
                    </el-table-column>
                    <el-table-column label="订单状态" align="center">
                        <template slot-scope="scope">{{scope.row.status | formatStatusType}}</template>
                    </el-table-column>
                    <el-table-column label="操作" align="center" width="200">
                        <template slot-scope="scope">
                            <el-button
                                    size="mini"
                                    @click="handleLookOrder(scope.$index, scope.row)"
                            >
                                查看订单
                            </el-button>
                            <el-button
                                size="mini"
                                type="danger"
                                @click="handleCloseOrder(scope.$index, scope.row)"
                                v-show="scope.row.status === 0"
                            >关闭订单</el-button>
                            <el-button
                                    size="mini"
                                    type="warning"
                                    @click="handleDeliveryOrder(scope.$index, scope.row)"
                                    v-show="scope.row.status === 1"
                            >订单发货</el-button>
                            <el-button
                                    size="mini"
                                    type="primary"
                                    @click="handleLookLogistics(scope.$index, scope.row)"
                                    v-show="scope.row.status === 2 || scope.row.status === 3"
                            >订单跟踪</el-button>
                            <el-button
                                    size="mini"
                                    type="danger"
                                    @click="handleDeleteOrder(scope.$index, scope.row)"
                                    v-show="scope.row.status === 4"
                            >删除订单</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <!--批量-->
            <div class="many-operate-container">
                <el-select
                        size="small"
                        placeholder="批量操作列表"
                        v-model="operatesType"
                >
                    <el-option
                            v-for="item in operatesOptions"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value"
                    />
                </el-select>
                <el-button
                        style="margin-left: 10px;"
                        size="mini"
                        type="danger"
                        @click="handleManyOperate()"
                >
                    确定
                </el-button>
            </div>
            <!--分页-->
            <div class="pagination-container">
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="listQuery.pageNum"
                        :page-sizes="[5, 10, 15]"
                        :page-size="listQuery.pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="total">
                </el-pagination>
            </div>
        </div>
        <!--物流跟踪-->
        <logistics-dialog v-model="logisticsDialogVisible"></logistics-dialog>
        <!--关闭订单-->
        <el-dialog
            title="关闭订单"
            :visible.sync="closeOrder.dialogVisible"
            width="30%"
            >
            <div>
                <el-input
                    type="textarea"
                    :rows="5"
                    placeholder="请输入关闭订单的原因"
                    v-model="closeOrder.content">
                </el-input>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="closeOrder.dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="handleCloseOrderConfirm">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    // 引入组件
    import  LogisticsDialog from './components/logisticsDialog'
    import {getOrderList, closeOrder, deleteOrder} from './../../api/orderApi'
    const defaultListQuery = {
        pageNum: 1,
        pageSize: 10,
        orderSn: null, // 订单编号
        status: null, // 订单状态
        orderType: null, // 订单分类
        sourceType: null, // 订单来源
        orderTime: null, // 下单时间
        receiverName: null, // 收货人姓名
    };
    export default {
        name: "index",
        data(){
           return {
               listQuery: Object.assign({}, defaultListQuery),
               // 订单的状态
               // 订单状态：0->待付款；1->待发货；2->已发货；3->已完成；4->已关闭；5->无效订单
               statusOptions: [
                   {label: '待付款', value: 0},
                   {label: '待发货', value: 1},
                   {label: '已发货', value: 2},
                   {label: '已完成', value: 3},
                   {label: '已关闭', value: 4},
                   {label: '无效订单', value: 5}
               ],
               // 订单的分类
               orderTypeOptions: [
                   {label: '正常订单', value: 0},
                   {label: '活动订单', value: 1}
               ],
               // 订单的来源
               sourceTypeOptions: [
                   {label: 'PC订单', value: 0},
                   {label: 'APP订单', value: 1}
               ],
               // 批量操作
               operatesOptions: [
                   {label: '批量发货', value: 1},
                   {label: '关闭订单', value: 2},
                   {label: '删除订单', value: 3}
               ],
               // 列表的数据
               list: [],
               //  总记录数
               total: null,
               // 加载动画控制
               listLoading: false,
               // 控制物流面板的显示和隐藏
               logisticsDialogVisible: false,
               // 关闭订单
               closeOrder: {
                   dialogVisible: false,
                   content: null,
                   orderIds: []
               },
               // 批量操作
               operatesType: null,
               // 存放多选的订单内容
               multipleSelection: [],
           }
        },
        components: {
            LogisticsDialog
        },
        created(){
            this.getList();
        },
        methods:{
            // 0. 物流跟踪
            handleLookLogistics(index, row){
                this.logisticsDialogVisible = true;
            },
            // 1. 搜索列表
            handleSearchList(){
                this.listQuery.pageNum = 1;
                this.getList();
            },
            // 2. 清空搜索条件
            handleResetSearch(){
               this.listQuery = Object.assign({}, defaultListQuery);
               this.getList();
            },
            // 3. 请求网络数据
            async getList(){
                let result = await getOrderList(this.listQuery);
                if(result && result.status === 1){
                      this.list = result.data.list;
                      this.total = result.data.total;
                }
            },
            // 4. 多选处理
            handleSelectionChange(val){
                this.multipleSelection = val;
            },
            // 5. 查看订单
            handleLookOrder(index, row){
               this.$router.push({
                  path: '/om/detail',
                  query: {id: row.id}
               });
            },
            // 6. 关闭订单
            handleCloseOrder(index, row){
                this.closeOrder.dialogVisible = true;
                this.closeOrder.orderIds = [row.id];
            },
            // 确认关闭订单
            handleCloseOrderConfirm(){
                if(this.closeOrder.content === null || this.closeOrder.content === ''){
                    this.$message({
                        message: '关闭订单的理由不能为空!',
                        type: 'warning',
                        duration: 1000
                    });
                    return;
                }
                // 发起关闭订单的请求
                closeOrder({ids: this.closeOrder.orderIds, note: this.closeOrder.content}).then((response)=>{
                    if(response.status === 1){
                        // 清空所有要关闭的订单id
                        this.closeOrder.orderIds = [];
                        // 隐藏面板
                        this.closeOrder.dialogVisible = false;
                        // 请求列表数据
                        this.getList();
                        this.$message({
                            message: '关闭订单成功!',
                            type: 'success',
                            duration: 1000
                        });
                    }
                }).catch((error)=>{
                    this.$message({
                        message: error.msg,
                        type: 'error',
                        duration: 1000
                    });
                });
            },
            // 7. 改变每页显示的条数
            handleSizeChange(val) {
                this.listQuery.pageNum = 1;
                this.listQuery.pageSize = val;
                // 重新加载数据
                this.getList();
            },
            // 8. 当前页码发生改变
            handleCurrentChange(val) {
                this.listQuery.pageNum = val;
                // 重新加载数据
                this.getList();
            },
            // 9. 订单发货
            handleDeliveryOrder(index, row){
                let listItem = this.dealProductOrder(row);
                this.$router.push({
                    path: '/om/deliver',
                    query: {listData: [listItem]}
                })
            },
            // 10. 删除订单
            handleDeleteOrder(index, row){
               let ids = []; // 所有订单
                ids.push(row.id);
                this.deleteOrder(ids);
            },
            deleteOrder(ids){
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                     // 调用删除订单的接口
                     deleteOrder({ids}).then((response)=>{
                         if(response.status === 1){
                             this.getList();
                             this.$message({
                                 message: '删除订单成功!',
                                 type: 'success',
                                 duration: 1000
                             });
                         }
                     }).catch((error)=>{
                         this.$message({
                             message: error.msg,
                             type: 'error',
                             duration: 1000
                         });
                     });
                });
            },
            // 11. 批量操作
            handleManyOperate(){
               // 11.1 判断订单不能为空
               if(this.multipleSelection === null || this.multipleSelection.length <= 0){
                   this.$message({
                       message: '请选择要批量操作的订单!',
                       type: 'warning',
                       duration: 1000
                   });
                   return;
               }

               // 11.2  批量发货
                if(this.operatesType === 1){
                    let list = [];
                    for(let i=0; i<this.multipleSelection.length; i++){
                        let order = this.multipleSelection[i];
                        // 筛选所有待发货的订单
                        if(order.status === 1){
                            list.push(this.dealProductOrder(order));
                        }
                    }
                    if(list.length === 0){
                        this.$message({
                            message: '选中的订单中没有要进行发货的!',
                            type: 'warning',
                            duration: 1000
                        });
                        return;
                    }
                    // 跳转至批量发货
                    this.$router.push({
                        path: '/om/deliver',
                        query: {listData: list}
                    });

                }else if(this.operatesType === 2){
                    // 11.3  批量关闭订单
                    for(let i=0; i<this.multipleSelection.length; i++){
                        // 取出已经关闭的订单
                        if(this.multipleSelection[i].status !== 4){
                            this.closeOrder.orderIds.push(this.multipleSelection[i].id);
                        }
                    }
                    this.closeOrder.dialogVisible = true;
                }else if(this.operatesType === 3){
                    // 11.4  批量删除订单
                    let ids = [];
                    for(let i=0; i<this.multipleSelection.length; i++){
                        ids.push(this.multipleSelection[i].id);
                    }
                    this.deleteOrder(ids);
                }else {
                    this.$message({
                        message: '请选择要批量操作的行为!',
                        type: 'warning',
                        duration: 1000
                    });
                }
            },
            // 12. 处理发货的接受数据
            dealProductOrder(order){
                return {
                    orderId: order.id,
                    orderSn: order.orderSn,
                    receiverName: order.receiverName,
                    receiverPhone: order.receiverPhone,
                    receiverPostCode: order.receiverPostCode,
                    address: order.receiverProvince + order.receiverCity + order.receiverRegion + order.receiverDetailAddress,
                    deliveryCompany: null,
                    deliverySn: null
                }
            }
        },
        filters: {
            formatPayType(value){
                if(value === 1){
                    return '支付宝';
                }else if(value === 2){
                    return '微信';
                } else {
                    return '未支付';
                }
            },
            formatSourceType(value){
                if(value === 0){
                    return 'PC订单';
                }else if(value === 1){
                    return 'APP订单';
                } else {
                    return '其它订单';
                }
            },
            formatStatusType(value){
                if(value === 0){
                    return '待付款';
                }else if(value === 1){
                    return '待发货';
                }else if(value === 2){
                    return '已发货';
                }else if(value === 3){
                    return '已完成';
                }else if(value === 4){
                    return '已关闭';
                }else if(value === 5){
                    return '无效订单';
                }
            }
        }
    }
</script>

<style scoped>

</style>
